import { HTMLAttributes } from 'react';
import { cn } from '@/utils/helpers';

interface CardProps extends HTMLAttributes<HTMLDivElement> {
  variant?: 'default' | 'bordered' | 'elevated' | 'glass' | 'dark';
  hover3d?: boolean;
  glow?: boolean;
}

export function Card({
  className,
  variant = 'default',
  hover3d = false,
  glow = false,
  children,
  ...props
}: CardProps) {
  const variants = {
    default: 'bg-white',
    bordered: 'bg-white border-2 border-gray-200 hover:border-primary-300',
    elevated: 'bg-white shadow-lg hover:shadow-xl',
    glass: 'bg-white/80 backdrop-blur-md border border-white/20 shadow-xl',
    dark: 'bg-gradient-to-br from-mine-dark to-mine-darker border border-mine-medium text-white',
  };

  const hoverEffects = hover3d ? 'card-3d' : '';
  const glowEffect = glow ? 'rarity-glow' : '';

  return (
    <div
      className={cn(
        'rounded-xl p-6 transition-all duration-300',
        variants[variant],
        hoverEffects,
        glowEffect,
        className
      )}
      {...props}
    >
      {children}
    </div>
  );
}

export function CardHeader({ className, children, ...props }: HTMLAttributes<HTMLDivElement>) {
  return (
    <div className={cn('mb-4', className)} {...props}>
      {children}
    </div>
  );
}

export function CardTitle({ className, children, ...props }: HTMLAttributes<HTMLHeadingElement>) {
  return (
    <h3 className={cn('text-2xl font-bold text-gray-900', className)} {...props}>
      {children}
    </h3>
  );
}

export function CardContent({ className, children, ...props }: HTMLAttributes<HTMLDivElement>) {
  return (
    <div className={cn('space-y-4', className)} {...props}>
      {children}
    </div>
  );
}
