import { BrowserRouter, Routes, Route, Link } from 'react-router-dom';
import { WagmiProvider } from 'wagmi';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { RainbowKitProvider, ConnectButton } from '@rainbow-me/rainbowkit';
import { config } from './utils/wagmi';
import { HomePage } from './pages/HomePage';
import { MinePage } from './pages/MinePage';
import { MintPage } from './pages/MintPage';
import { InventoryPage } from './pages/InventoryPage';
import { RewardsPage } from './pages/RewardsPage';
import { Hammer, Home, Package, Coins } from 'lucide-react';

import '@rainbow-me/rainbowkit/styles.css';

const queryClient = new QueryClient();

function App() {
  return (
    <WagmiProvider config={config}>
      <QueryClientProvider client={queryClient}>
        <RainbowKitProvider>
          <BrowserRouter>
            <div className="min-h-screen bg-gray-50">
              <Navigation />
              <main>
                <Routes>
                  <Route path="/" element={<HomePage />} />
                  <Route path="/mine" element={<MinePage />} />
                  <Route path="/mint" element={<MintPage />} />
                  <Route path="/inventory" element={<InventoryPage />} />
                  <Route path="/rewards" element={<RewardsPage />} />
                </Routes>
              </main>
            </div>
          </BrowserRouter>
        </RainbowKitProvider>
      </QueryClientProvider>
    </WagmiProvider>
  );
}

function Navigation() {
  return (
    <nav className="bg-white border-b border-gray-200">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-16">
          {/* Logo */}
          <Link to="/" className="flex items-center gap-2 font-bold text-xl text-gray-900">
            <Hammer className="w-6 h-6 text-primary-600" />
            Zama Miner
          </Link>

          {/* Navigation Links */}
          <div className="hidden md:flex items-center gap-6">
            <NavLink to="/" icon={<Home className="w-4 h-4" />}>
              首页
            </NavLink>
            <NavLink to="/mint" icon={<Hammer className="w-4 h-4" />}>
              铸造
            </NavLink>
            <NavLink to="/mine" icon={<Hammer className="w-4 h-4" />}>
              挖矿
            </NavLink>
            <NavLink to="/inventory" icon={<Package className="w-4 h-4" />}>
              背包
            </NavLink>
            <NavLink to="/rewards" icon={<Coins className="w-4 h-4" />}>
              奖励
            </NavLink>
          </div>

          {/* Connect Button */}
          <ConnectButton />
        </div>
      </div>
    </nav>
  );
}

function NavLink({
  to,
  icon,
  children,
}: {
  to: string;
  icon: React.ReactNode;
  children: React.ReactNode;
}) {
  return (
    <Link
      to={to}
      className="flex items-center gap-1.5 text-gray-600 hover:text-primary-600 transition-colors"
    >
      {icon}
      <span>{children}</span>
    </Link>
  );
}

export default App;
